  <b-container class="padding-top expand-width">
    <b-form @submit="AddCharacter()">
      <b-row>
        <b-col md="9" class="padding">
          <b-form-input
            class="style-inputField-scheme"
            v-model="CharacterName"
            placeholder="Character Name"
            autocomplete="off"
            required
          ></b-form-input>
        </b-col>
        <b-col md="3" class="padding">
          <b-button type="submit" class="style-button-scheme">Add</b-button>
        </b-col>
      </b-row>
    </b-form>
