      <!--Warriors-->
      <b-col md="3" class="padding-top">
        <div class="characterCount">
          <p class="warriors">
            Warriors: <span>{{ warriors.length }}</span>
          </p>
        </div>
        <vue-custom-scrollbar class="scroll-area" :settings="scrollSettings">
          <draggable
            :list="warriors"
            group="all-characters"
            @sort="onSort('warriors')"
            tag="ul"
            :animation="200"
            ghost-class="moving-card"
            filter=".action-button"
            class="mr-3"
          >
            <li
              v-for="(warrior, index) in warriors"
              :key="warrior.id"
              class="mb-2 bg-white cursor-move rounded-lg"
              
            >
              <div
                class="card-info p-2 flex rounded-t-lg justify-between items-center border-b-2"
              >
                <b-form-checkbox
                  switch
                  v-model="warrior.guildlead"
                  @change="onUpdate('warriors', index)"
                  class="style-checkbox-scheme"
                ></b-form-checkbox>
                <div class="flex">
                  <crown v-if="warrior.guildlead" :size="20" />
                  <crownOutline v-if="index === 0" :size="20" />
                  <p
                    class="ml-2 text-gray-900 font-semibold font-sans tracking-wide"
                  >
                    {{ warrior.name }}
                  </p>
                </div>
                <div>
                  <button
                    aria-label="Edit user"
                    class="p-1 focus:outline-none focus:shadow-outline text-teal-500 hover:text-teal-600"
                  >
                    <EditIcon /></button
                  ><button
                    aria-label="Delete user"
                    class="p-1 focus:outline-none focus:shadow-outline text-red-500 hover:text-red-600"
                    @click="onDelete('warriors', warrior.name, index)"
                  >
                    <Trash2Icon />
                  </button>
                </div>
              </div>
              <div class="card-add rounded-b-lg bg-gray-200 px-4">
                <div class="flex justify-center">
                  <p class="text-gray-900 font-semibold text-center mb-2">
                    {{ lastWednesday }} - Now
                  </p>
                </div>
                <div
                  v-for="(item, itemIndex) in warrior.items"
                  :key="item.index"
                >
                  <a
                    v-if="checkDate('warriors', index, itemIndex)"
                    v-bind:data-wowhead="'domain=classic&item=' + item.id"
                    class="flex justify-between border-b-2 cursor-default"
                  >
                    <span
                      v-if="item.name.length < 24"
                      class="text-gray-900 font-sans"
                    >
                      {{ item.name }}
                    </span>
                    <span v-else class="text-gray-900 font-sans">
                      {{ item.name.substr(0, 24) + "..." }}
                    </span>
                    <span class="text-gray-900 font-sans">{{ item.dkp }}</span>
                  </a>
                </div>
                <div class="flex justify-center">
                  <button
                    aria-label="Add Item to user"
                    @click="toggleItemList('warriors', index)"
                    class="hover:text-red-600"
                  >
                    <plus />
                  </button>
                </div>
              </div>
            </li>
