                    <Trash2Icon />
                  </button>
                </div>
              </div>
              <div class="card-add rounded-b-lg bg-gray-200 px-4">
                <div class="flex justify-center">
                  <p class="text-gray-900 font-semibold text-center mb-2">
                    {{ lastWednesday }} - Now
                  </p>
                </div>
                <div v-for="(item, itemIndex) in druid.items" :key="item.index">
                  <a
                    v-if="checkDate('druids', index, itemIndex)"
                    v-bind:data-wowhead="'domain=classic&item=' + item.id"
                    class="flex justify-between border-b-2 cursor-default"
                  >
                    <span
                      v-if="item.name.length < 24"
                      class="text-gray-900 font-sans"
                    >
                      {{ item.name }}
                    </span>
                    <span v-else class="text-gray-900 font-sans">
                      {{ item.name.substr(0, 24) + "..." }}
                    </span>
                    <span class="text-gray-900 font-sans">{{ item.dkp }}</span>
                  </a>
                </div>
                <div class="flex justify-center">
                  <button
                    aria-label="Add Item to user"
                    @click="toggleItemList('druids', index)"
                    class="hover:text-red-600"
                  >
                    <plus />
                  </button>
                </div>
              </div>
            </li>
          </draggable>
        </vue-custom-scrollbar>
      </b-col>
    </b-row>

    <!-- itemList -->
    <itemList
      v-if="itemListOn"
      v-bind:characterName="characterNameToItemList"
      v-bind:characterClass="characterClassToItemList"
      v-bind:openAddItem=true
      v-on:closeItemList="closeItemList"
      v-on:addItemFinal="addItemFinal"
    />

    <!-- loader -->
    <span v-if="isLoading" class="loader"
      ><span class="loader-inner"></span
    ></span>
  </b-container>
</template>

<script>
import { EditIcon, Trash2Icon } from "vue-feather-icons";
import Draggable from "vuedraggable";
import crown from "vue-material-design-icons/Crown.vue";
import crownOutline from "vue-material-design-icons/CrownOutline.vue";
import plus from "vue-material-design-icons/Plus.vue";
import { mapActions, mapGetters } from "vuex";
import vueCustomScrollbar from "vue-custom-scrollbar";
import itemList from "@/components/itemList.vue";

export default {
  name: "dashboard",
  components: {
    EditIcon,
    Trash2Icon,
    crown,
    crownOutline,
    plus,
    Draggable,
    vueCustomScrollbar,
    itemList
  },
  data() {
    return {
      CharacterName: "",
      warriors: [],
      priests: [],
      paladins: [],
      hunters: [],
      rogues: [],
      mages: [],
      warlocks: [],
      druids: [],
      scrollSettings: {
        minScrollbarLength: 40,
        maxScrollbarLength: 60,
        suppressScrollX: true,
        wheelPropagation: false
      },
      itemListOn: false,
      characterNameToItemList: "",
      characterClassToItemList: "",
      lastWednesday: "",
      whTooltips: { colorLinks: true, iconizeLinks: true, renameLinks: true },
      //loading animation to push data
      isLoading: false,
      //sleep timer to push date
      sleeptimerDefault: 2000
    };
  },
  computed: {
    ...mapGetters(["GetWarriors"]),
    ...mapGetters(["GetPriests"]),
    ...mapGetters(["GetPaladins"]),
    ...mapGetters(["GetHunters"]),
    ...mapGetters(["GetRogues"]),
    ...mapGetters(["GetMages"]),
    ...mapGetters(["GetWarlocks"]),
    ...mapGetters(["GetDruids"])
  },
  mounted() {
    let recaptchaScript = document.createElement("script");
    recaptchaScript.setAttribute(
      "src",
      "https://wow.zamimg.com/widgets/power.js"
    );
    document.head.appendChild(recaptchaScript);
  },
  created() {
    this.FetchWarriors();
    this.warriors = this.GetWarriors;
    this.FetchPriests();
    this.priests = this.GetPriests;
    this.FetchPaladins();
    this.paladins = this.GetPaladins;
    this.FetchHunters();
    this.hunters = this.GetHunters;
    this.FetchRogues();
    this.rogues = this.GetRogues;
    this.FetchMages();
    this.mages = this.GetMages;
    this.FetchWarlocks();
    this.warlocks = this.GetWarlocks;
    this.FetchDruids();
    this.druids = this.GetDruids;
  },
  methods: {
    //Fetch all classes
    ...mapActions(["FetchWarriors"]),
    ...mapActions(["FetchPriests"]),
    ...mapActions(["FetchPaladins"]),
    ...mapActions(["FetchHunters"]),
    ...mapActions(["FetchRogues"]),
    ...mapActions(["FetchMages"]),
    ...mapActions(["FetchWarlocks"]),
    ...mapActions(["FetchDruids"]),

    //Delete one charatcer in collection
    ...mapActions(["deleteCharacter"]),

    //Add new Character
    ...mapActions(["addCharacter"]),

    //Save all character of one class
    ...mapActions(["saveCharacters"]),

    //Update an existing character
    ...mapActions(["updateCharacter"]),

    //Add character. Default warriors
    AddCharacter() {
      if (this.CharacterName.length <= 12) {
        this.warriors.push({
          name: this.CharacterName,
          guildlead: false,
          items: []
        });
        this.addCharacter({
          id: this.warriors.length - 1,
          class: "warriors",
          name: this.warriors[this.warriors.length - 1].name,
          guildlead: this.warriors[this.warriors.length - 1].guildlead,
          items: this.warriors[this.warriors.length - 1].items
        });
      } else {
        alert("Too many chars");
      }
      this.CharacterName = "";
    },

    //timeout for loading animation
    sleep(ms) {
      return new Promise(resolve => setTimeout(resolve, ms));
    },

    //Update character and push to firebase, Fired by event addItemFinal + GuildLeadChange
    onUpdate(characterClass, index) {
      this.isLoading = true;
      if (characterClass === "warriors") {
        this.updateCharacter({
          class: characterClass,
          character: this.warriors[index]
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "priests") {
        this.updateCharacter({
          class: characterClass,
          character: this.priests[index]
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "paladins") {
        this.updateCharacter({
          class: characterClass,
          character: this.paladins[index]
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "hunters") {
        this.updateCharacter({
          class: characterClass,
          character: this.hunters[index]
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "rogues") {
        this.updateCharacter({
          class: characterClass,
          character: this.rogues[index]
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "mages") {
        this.updateCharacter({
          class: characterClass,
          character: this.mages[index]
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "warlocks") {
        this.updateCharacter({
          class: characterClass,
          character: this.warlocks[index]
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "druids") {
        this.updateCharacter({
          class: characterClass,
          character: this.druids[index]
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else {
        alert("undefined in onUpdate()");
      }
    },

    //Character delete. Push new date if confirmed
    onDelete(characterClass, characterName, index) {
      if (confirm("Are your sure? This can't be undone")) {
        this.isLoading = true;
        if (characterClass === "warriors") {
          this.warriors.splice(index, 1);
          this.deleteCharacter({
            class: characterClass,
            name: characterName
          }).then(async response => {
            await this.sleep(this.sleeptimerDefault);
            this.isLoading = false;
            return response;
          });
        } else if (characterClass === "priests") {
          this.priests.splice(index, 1);
          this.deleteCharacter({
            class: characterClass,
            name: characterName
          }).then(async response => {
            await this.sleep(this.sleeptimerDefault);
            this.isLoading = false;
            return response;
          });
        } else if (characterClass === "paladins") {
          this.paladins.splice(index, 1);
          this.deleteCharacter({
            class: characterClass,
            name: characterName
          }).then(async response => {
            await this.sleep(this.sleeptimerDefault);
            this.isLoading = false;
            return response;
          });
        } else if (characterClass === "hunters") {
          this.hunters.splice(index, 1);
          this.deleteCharacter({
            class: characterClass,
            name: characterName
          }).then(async response => {
            await this.sleep(this.sleeptimerDefault);
            this.isLoading = false;
            return response;
          });
        } else if (characterClass === "rogues") {
          this.rogues.splice(index, 1);
          this.deleteCharacter({
            class: characterClass,
            name: characterName
          }).then(async response => {
            await this.sleep(this.sleeptimerDefault);
            this.isLoading = false;
            return response;
          });
        } else if (characterClass === "mages") {
          this.mages.splice(index, 1);
          this.deleteCharacter({
            class: characterClass,
            name: characterName
          }).then(async response => {
            await this.sleep(this.sleeptimerDefault);
            this.isLoading = false;
            return response;
          });
        } else if (characterClass === "warlocks") {
          this.warlocks.splice(index, 1);
          this.deleteCharacter({
            class: characterClass,
            name: characterName
          }).then(async response => {
            await this.sleep(this.sleeptimerDefault);
            this.isLoading = false;
            return response;
          });
        } else if (characterClass === "druids") {
          this.druids.splice(index, 1);
          this.deleteCharacter({
            class: characterClass,
            name: characterName
          }).then(async response => {
            await this.sleep(this.sleeptimerDefault);
            this.isLoading = false;
            return response;
          });
        } else {
          alert("undefined in onDelete()");
        }
      }
    },

    onSort(characterClass) {
      this.isLoading = true;
      if (characterClass === "warriors") {
        this.saveCharacters({
          class: characterClass,
          characters: this.warriors
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "priests") {
        this.saveCharacters({
          class: characterClass,
          characters: this.priests
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "paladins") {
        this.saveCharacters({
          class: characterClass,
          characters: this.paladins
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "hunters") {
        this.saveCharacters({
          class: characterClass,
          characters: this.hunters
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "rogues") {
        this.saveCharacters({
          class: characterClass,
          characters: this.rogues
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "mages") {
        this.saveCharacters({
          class: characterClass,
          characters: this.mages
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "warlocks") {
        this.saveCharacters({
          class: characterClass,
          characters: this.warlocks
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else if (characterClass === "druids") {
        this.saveCharacters({
          class: characterClass,
          characters: this.druids
        }).then(async response => {
          await this.sleep(this.sleeptimerDefault);
          this.isLoading = false;
          return response;
        });
      } else {
        alert("undefined in onSort()");
      }
    },

    // variables get send to itemList
    toggleItemList(characterClass, index) {
      if (characterClass === "warriors") {
        this.characterNameToItemList = this.warriors[index].name;
      } else if (characterClass === "priests") {
        this.characterNameToItemList = this.priests[index].name;
      } else if (characterClass === "paladins") {
        this.characterNameToItemList = this.paladins[index].name;
      } else if (characterClass === "hunters") {
        this.characterNameToItemList = this.hunters[index].name;
      } else if (characterClass === "rogues") {
        this.characterNameToItemList = this.rogues[index].name;
      } else if (characterClass === "mages") {
        this.characterNameToItemList = this.mages[index].name;
      } else if (characterClass === "warlocks") {
        this.characterNameToItemList = this.warlocks[index].name;
      } else if (characterClass === "druids") {
        this.characterNameToItemList = this.druids[index].name;
      } else {
        alert("undefined in toggleItemList()");
      }
      this.characterClassToItemList = characterClass;
      this.itemListOn = !this.itemListOn;
    },

    //close ItemList
    closeItemList() {
      this.itemListOn = false;
    },

    //Event fired from itemList, which is getting an event from addItem
    addItemFinal(evt) {
      let pointer = 0;
      if (evt.characterClass === "warriors") {
        for (let index = 0; index < this.warriors.length; index++) {
          if (this.warriors[index].name === evt.characterName) {
            let item = {
              id: evt.itemID,
              name: evt.itemName,
              date: evt.date,
              dkp: evt.dkp
            };
            this.warriors[index].items.push(item);
            pointer = index;
            break;
          }
        }
      } //ToDo !
      else if (evt.characterClass === "priests") {
        for (let index = 0; index < this.priests.length; index++) {
          if (this.priests[index].name === evt.characterName) {
            let item = {
              id: evt.itemID,
              name: evt.itemName,
              date: evt.date,
              dkp: evt.dkp
            };
            this.priests[index].items.push(item);
            pointer = index;
            break;
          }
        }
      } else if (evt.characterClass === "paladins") {
        for (let index = 0; index < this.paladins.length; index++) {
          if (this.paladins[index].name === evt.characterName) {
            let item = {
              id: evt.itemID,
              name: evt.itemName,
              date: evt.date,
              dkp: evt.dkp
            };
            this.paladins[index].items.push(item);
            pointer = index;
            break;
          }
        }
      } else if (evt.characterClass === "hunters") {
        for (let index = 0; index < this.hunters.length; index++) {
          if (this.hunters[index].name === evt.characterName) {
            let item = {
              id: evt.itemID,
              name: evt.itemName,
              date: evt.date,
              dkp: evt.dkp
            };
            this.hunters[index].items.push(item);
            pointer = index;
            break;
          }
        }
      } else if (evt.characterClass === "rogues") {
        for (let index = 0; index < this.rogues.length; index++) {
          if (this.rogues[index].name === evt.characterName) {
            let item = {
              id: evt.itemID,
              name: evt.itemName,
              date: evt.date,
              dkp: evt.dkp
            };
            this.rogues[index].items.push(item);
            pointer = index;
            break;
          }
        }
      } else if (evt.characterClass === "mages") {
        for (let index = 0; index < this.mages.length; index++) {
          if (this.mages[index].name === evt.characterName) {
            let item = {
              id: evt.itemID,
              name: evt.itemName,
              date: evt.date,
              dkp: evt.dkp
            };
            this.mages[index].items.push(item);
            pointer = index;
            break;
          }
        }
      } else if (evt.characterClass === "warlocks") {
        for (let index = 0; index < this.warlocks.length; index++) {
          if (this.warlocks[index].name === evt.characterName) {
            let item = {
              id: evt.itemID,
              name: evt.itemName,
              date: evt.date,
              dkp: evt.dkp
            };
            this.warlocks[index].items.push(item);
            pointer = index;
            break;
          }
        }
      } else if (evt.characterClass === "druids") {
        for (let index = 0; index < this.druids.length; index++) {
          if (this.druids[index].name === evt.characterName) {
            let item = {
              id: evt.itemID,
              name: evt.itemName,
              date: evt.date,
              dkp: evt.dkp
            };
            this.druids[index].items.push(item);
            pointer = index;
            break;
          }
        }
      } else {
        alert("undefined in addItemFinal()");
      }
      this.onUpdate(evt.characterClass, pointer);
    },

    //Check current date and last Wednesday
    checkDate(characterClass, index, itemIndex) {
      let moment = require("moment");
      let currentday = moment().day();
      let date = "";
      if (currentday >= 3)
        this.lastWednesday = moment()
          .day(3)
          .format("DD/MM/YYYY")
          .toString();
      else {
        this.lastWednesday = moment()
          .day(-4)
          .format("DD/MM/YYYY")
          .toString();
      }
      if (characterClass === "warriors") {
        date = this.warriors[index].items[itemIndex].date;
        
      } else if (characterClass === "priests") {
        date = this.priests[index].items[itemIndex].date;
      } else if (characterClass === "paladins") {
        date = this.paladins[index].items[itemIndex].date;
      } else if (characterClass === "hunters") {
        date = this.hunters[index].items[itemIndex].date;
      } else if (characterClass === "rogues") {
        date = this.rogues[index].items[itemIndex].date;
      } else if (characterClass === "mages") {
        date = this.mages[index].items[itemIndex].date;
      } else if (characterClass === "warlocks") {
        date = this.warlocks[index].items[itemIndex].date;
      } else if (characterClass === "druids") {
        date = this.druids[index].items[itemIndex].date;
      } else {
        alert("undefined in onSort()");
      } 
      if (moment(date, "DD/MM/YYYY").isSameOrAfter(moment(this.lastWednesday, "DD/MM/YYYY"), "day")) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>

<style scoped>
.moving-card {
  @apply opacity-50 bg-gray-100 border border-blue-500;
}
.scroll-area {
  position: relative;
  margin: auto;
  height: 50vh;
}

.characterCount {
  border-bottom: solid 1px white;
  margin-bottom: 1em;
}
.characterCount > p > span {
  font-weight: bold;
}
[v-cloak] {
  background-color:white;
}

.loader {
  position: fixed;
  right: 10px;
  bottom: 10px;
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 4px solid #d98f40;
  animation: loader 2s infinite ease;
}
.loader-inner {
  vertical-align: top;
  display: inline-block;
  width: 100%;
  background-color: #d98f40;
  animation: loader-inner 2s infinite ease-in;
}
@keyframes loader {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(180deg);
  }

  50% {
    transform: rotate(180deg);
  }

  75% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
@keyframes loader-inner {
  0% {
    height: 0%;
  }

  25% {
    height: 0%;
  }

  50% {
    height: 100%;
  }

  75% {
    height: 100%;
  }

  100% {
    height: 0%;
  }
}
</style>
